    display: flex;
  
    align-items: center;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
    border-radius: 25px;
    margin: 30px;
  } */

  @media screen and (min-width: 400px) {
    .cartItem {
      width: 350px;
      height: 250px;
      display: flex;
      align-items: center;
      box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
      border-radius: 25px;
      margin: 30px;
    }
  }

  @media screen and (min-width: 550px) {
    .cartItem {
      width: 500px;
      height: 250px;
      display: flex;
      align-items: center;
      box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
      border-radius: 25px;
      margin: 30px;
    }
  }

  @media screen and (min-width: 700px) {
    .cartItem {
      width: 700px;
      height: 250px;
      display: flex;
      align-items: center;
      box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
      border-radius: 25px;
      margin: 30px;
    }
  }
  
  .cartItem img {
    width: 200px;
  }
  
  .cartItem .description {
    width: 100%;
    font-size: 20px;
  }
  
  .countHandler input {
    width: 40px;
    text-align: center;
    font-weight: bolder;
  }
  
  .checkout button {
    width: 150px;
    height: 50px;
    background-color: rgb(19, 19, 19);
    color: white;
    border: none;
    border-radius: 8px;
    margin: 10px;
    cursor: pointer;
  }

  .btnbutton {
    margin: 2px;
    background-color: transparent;
    border: 2px solid rgb(19, 19, 19);
    width: auto;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 15px;
  }
  
  .btnbutton:hover {
    background-color: rgb(19, 19, 19);
    color: white;
    cursor: pointer;
  }