.cart {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  /* .cartItem {
    width: 700px;
    height: 250px;
    display: flex;
  
    align-items: center;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
    border-radius: 25px;
    margin: 30px;
  } */
  @media screen and (min-width: 400px) {
    .cartItem {
      width: 350px;
      height: 250px;
      display: flex;
      align-items: center;
      box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
      border-radius: 25px;
      margin: 30px;
    }
  }
  
  @media screen and (min-width: 550px) {
    .cartItem {
      width: 500px;
      height: 250px;
      display: flex;
      align-items: center;
      box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
      border-radius: 25px;
      margin: 30px;
    }
  }
  @media screen and (min-width: 700px) {
    .cartItem {
      width: 700px;
      height: 250px;
      display: flex;
      align-items: center;
      box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
      border-radius: 25px;
      margin: 30px;
    }
  }
  
  .cartItem img {
    width: 200px;
  }
  
  .cartItem .description {
    width: 100%;
    font-size: 30px;
  }
  
  .countHandlerBtn input {
    width: 40px;
    text-align: center;
    font-weight: bolder;
  }
  
  .countTxt {
    margin-left: 4px;
    width: 80px;
    height: 35px;
    text-align: center;
    font-weight: bolder;
    margin-right: 4px;
  }
  
  .checkoutbtn button {
    width: auto;
    height: 50px;
    background-color: rgb(19, 19, 19);
    color: white;
    border: none;
    border-radius: 8px;
    margin: 10px;
    cursor: pointer;
  }